import { signIn } from 'next-auth/react';
import type { ReactElement } from 'react';
import { useEffect } from 'react';
import type { ComponentWithLayout } from '../../hooks/useLayout';
import Routes from '../../utils/routes';
const GoogleAuthPage: ComponentWithLayout = () => {
  useEffect(() => {
    signIn('google', {
      callbackUrl: Routes.AUTH_GOOGLE_SUCCESS
    });
  }, []);
  return null as unknown as ReactElement;
};
GoogleAuthPage.getLayout = (page: ReactElement) => page;
export default GoogleAuthPage;